.letter-input {
    /*margin: 0 0.15rem 0.3rem 0;*/
    margin: 0 0.15rem;
    padding: 0.5rem;
    /*border: 1px solid #333;*/
    width: 35px;
    height: 35px;
    text-align: center;
    font-size: 20px;
    text-transform: lowercase;
    font-weight: bolder !important;
    border-radius: 10px;
    font-family: 'Major Mono Display', monospace;
    border: none !important;
    outline: none !important;

}

.next-button {
    font-family: "Major Mono Display", sans-serif;
    border-radius: 10px;
    font-size: 14px;
    padding: 10px 30px 10px 30px;
    font-weight: 900;
    cursor:pointer;
}


.correct-answer {
    animation: MoveUpDown 0.5s linear infinite;
    position: relative;
    /*left: 0;*/
    /*top: 0;*/
}

@keyframes MoveUpDown {
    0%, 100% {
        top: -5px;
    }
    50% {
        top: 5px;
    }
}


h1{
    font-family: 'Major Mono Display', monospace;
}

h5{
    font-family: 'Major Mono Display', monospace;
}

h3{
    font-family: 'Major Mono Display', monospace;
    text-transform: lowercase;
    max-width: 300px;
}
